.sidebar-section {
  display: flex;
  background-color: black;
}

.sidebar {
  height: 100vh;
  /* background-color: #f4f4f4; */
  background-color: #1e1e2c;
  width: 90px;
  position: fixed;
}

.sidebar-toggle {
  width: 200px;
}

.sidebar-toggle-icons {
  display: flex;
  align-items: right;
  justify-content: right;
  cursor: pointer;
}

.sidebar-toggle-icons p svg {
  background-color: #f29f67;
  color: white;
  margin-top: 10px;
}

/* ========== MOBILE VIEW ========== */
@media (max-width: 600px) {
  .sidebar {
    display: none;
  }

  .sidebar-toggle {
    width: 100px;
  }

  .sidebar-toggle-icons p svg {
    margin-top: 5px;
  }
}
